import { NgModule, Component } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { CreativeComponent } from './creative/creative.component';
import { BusinessComponent } from './business/business.component';
import { NetworkComponent } from './network/network.component';
import { ServicesComponent } from './services/services.component';
import { CaseStudiesComponent } from './case-studies/case-studies.component';
import { CaseStudieComponent } from './case-studie/case-studie.component';
import { HomeCreativeComponent } from './home/creative/creative.component';
import { HomeBusinessComponent } from './home/business/business.component';
import { HomeNetworkComponent } from './home/network/network.component';

const routes: Routes = [
  { path: '',
  pathMatch: 'full',
    redirectTo: 'home/creative',
  },
  {
    path: 'home',
    component: HomeCreativeComponent,
    data : { animation: 'slideToRightCreative' }
  },
  {
    path: 'home/creative',
    component: HomeCreativeComponent,
    data : { animation: 'slideToRightCreative2' }
  },
  {
    path: 'home/business',
    component: HomeBusinessComponent,
    data : { animation: 'slideToRightBusiness' }
  },
  {
    path: 'home/network',
    component: HomeNetworkComponent,
    data : { animation: 'slideToRightNetwork' }
  },
  {
    path: 'creative',
    component: CreativeComponent,
    data : { animation: 'slideToRight' }
  },
  {
    path: 'business',
    component: BusinessComponent,
    data : { animation: 'slideToRight' }
  },
  {
    path: 'network',
    component: NetworkComponent,
    data : { animation: 'slideToLeft' }
  },
  {
    path: 'services',
    component: ServicesComponent,
    data : { animation: 'slideToTopService' }
  },
  {
    path: 'casestudies',
    component: CaseStudiesComponent,
    data : { animation: 'slideToFade' }
  },
  {
    path: 'casestudies/:slug',
    component: CaseStudieComponent,
    data : { animation: 'slideToRight2' }
  },

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
